.Button {
    background-color: white; 
    box-shadow: 0px 2px 2px grey;
    border: 2px rgb(38, 62, 82);
    border-radius: 5px;
    color: black;
    padding: 0px 5px;
    text-align: center;
    display: inline-block;
    margin: 5px;
    font-size: 11px;
}


.Button:active{
    background-color: rgb(214, 214, 214); 
    box-shadow: none;
}